<template>
    <div class="container">
        <div id="category">category ▼</div>
        <div id="title">Hello,</div>
        <ul id="menu">
            <li class="inner menu-1 active" @click="changeSelect(1)">
                <h4>Flowers & Butterflies</h4>
                <p>ITEM CODE FLO07</p>
            </li>
            <li class="inner menu-2" @click="changeSelect(2)">
                <h4>Fala Frangipani -Pink</h4>
                <p>ITEM CODE: FAL01</p>
            </li>
            <li class="inner menu-3" @click="changeSelect(3)">
                <h4>Whāriki Woven Mat</h4>
                <p>ITEM CODE: WHĀ01</p>
            </li>
            <li class="inner menu-4" @click="changeSelect(4)">
                <h4>Rambling Roses</h4>
                <p>ITEM CODE: ROS06</p>
            </li>
            <li class="inner menu-5" @click="changeSelect(5)">
                <h4>Breaking Waves</h4>
                <p>ITEM CODE: WAV01</p>
            </li>
            <li class="inner menu-6" @click="changeSelect(6)">
                <h4>Fala Frangipani – Yellow</h4>
                <p>ITEM CODE: FAL02</p>
            </li>
        </ul>
        <ul id="menu-index">
            <li class="index index-1 active" @click="changeSelect(1)">01</li>
            <li class="index index-2" @click="changeSelect(2)">02</li>
            <li class="index index-3" @click="changeSelect(3)">03</li>
            <li class="index index-4" @click="changeSelect(4)">04</li>
            <li class="index index-5" @click="changeSelect(5)">05</li>
            <li class="index index-6" @click="changeSelect(6)">06</li>
        </ul>
    </div>
</template>

<script>
    import {bus} from "../main";

    export default {
        name: 'MenuBar',
        props: {
            index: {
                type: "integer"
            }
        },
        data() {
            return {
                currentIndex: 1
            }
        },
        created() {
            bus.$on('changeImage', (data) => {
                this.goChange(data)
            })
        },
        methods: {
            goChange(index){
                this.selectMenu(index);
                this.changeIndex(index);
            },
            changeSelect(index){
                this.goChange(index);
                bus.$emit('changeIt', index);
            },
            selectMenu(index){
                var menuList = document.getElementById("menu");
                var menuItem = menuList.getElementsByClassName("inner");
                menuItem[index-1].className += ' active';
                menuItem[this.currentIndex -1].className = menuItem[this.currentIndex -1].className.replace(" active", "");
            },
            changeIndex(index){
                // Add active class to the current button (highlight it)
                var indexList = document.getElementById("menu-index");
                var listItems = indexList.getElementsByClassName("index");
                listItems[index-1].className += ' active';
                listItems[this.currentIndex -1].className = listItems[this.currentIndex -1].className.replace(" active", "");
                this.currentIndex = index;
            }
        }
    }
</script>

